import React,{useEffect,useState} from 'react' 
import axios from 'axios'
import './covid.css'

import { faRocket } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const Covid = () => {
    const [data,setData]=useState([])
   
    const getCovidData=async()=>{
        const res=await axios.get('https://api.covid19india.org/data.json');
        
        console.log(res.data.statewise[0])
        setData(res.data.statewise[0])
    }
    useEffect(() => {
       getCovidData();
        
    }, []);
    return (
        <div>
              <link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet"></link>

           <h1 class="heading">COVID TRACKER INDIA</h1>
        
       
          
            {/* <div class="about">
                <a class="bg_links social portfolio" href="https://www.rafaelalucas.com" target="_blank">
                    <span class="icon"></span>
                </a>
                <a class="bg_links social dribbble" href="https://dribbble.com/rafaelalucas" target="_blank">
                    <span class="icon"></span>
                </a>
                <a class="bg_links social linkedin" href="https://www.linkedin.com/in/rafaelalucas/" target="_blank">
                    <span class="icon"></span>
                </a>
                <a class="bg_links logo"></a>
            </div> */}
            <div class="content">
                {/* <div class="card">
                    <div class="icon"><i class="material-icons md-36">face</i></div>
                     <p class="title">INDIA</p>
                    <p class="text">Click to see or edit your profile page.</p>
                </div> */}
                <div class="card">
                    <div class="icon"><i class="material-icons md-36">favorite_border</i></div>
                    <p class="title">TOTAL RECOVERED</p>
                    <p class="text">{data.recovered}</p>
                </div>
                <div class="card">
                    <div class="icon"><i class="material-icons md-36">alternate_email</i></div>
                    <p class="title">TOTAL CONFIRMED</p>
                    <p class="text">{data.confirmed}</p>
                </div>
                <div class="card">
                    <div class="icon"><i class="material-icons md-36">face</i></div>
                     <p class="title">TOTAL DEATH</p>
                    <p class="text">{data.deaths}</p>
                </div>
                <div class="card">
                    <div class="icon"><i class="material-icons md-36">favorite_border</i></div>
                    <p class="title">TOTAL ACTIVE</p>
                    <p class="text">{data.active}</p>
                </div>
                <div class="card">
                    <div class="icon"><i class="material-icons md-36">alternate_email</i></div>
                    <p class="title">LAST UPDATED</p>
                    <p class="text">{data.lastupdatedtime}</p>
                </div>
            </div>
        </div>
    )
    }

export default Covid
